import React, { DragEventHandler, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import toFormat from "../../helpers/toFormat";
import { RootState } from "../../redux/store";
import { ITask } from "../../api/models/Task";
import addClass from "../../helpers/addClass";
import "./Task.scss"

interface Props extends ITask {
  onClick: Function;
  isCurrent?: boolean;
  dragStart?: DragEventHandler;
  onDrop?: DragEventHandler;
}

function Task(props: Props) {
  let {
    name,
    break_time,
    work_time,
    isCurrent = false,
    onClick,
    completed,
  } = props;
  let { dragStart, onDrop } = props;
  let app = useSelector((state: RootState) => state.app);
  let time_template = `${toFormat(work_time)} +${toFormat(break_time)}`;

  function dragEnter(e: any) {
    e.target.style.background = "lightgray";
  }

  function dragLeave(e: any) {
    e.target.style.background = "white";
  }

  function dragEnd(e: any) {
    e.target.style.background = "white";
  }

  function prevent(e: any) {
    e.preventDefault();
  }

  return (
    <div
      className={
        `task` +
        addClass(isCurrent, "current") +
        addClass(app.mode == "Break", "break") +
        addClass(completed, "completed")
      }
      onClick={() => onClick()}
      draggable={true}
      onDragStart={(e) => dragStart || prevent}
      onDragEnd={dragEnd}
      onDragOver={(e) => e.preventDefault()}
      onDragEnter={dragEnter}
      onDragLeave={dragLeave}
      onDrop={onDrop || prevent}
    >
      <div className="task__name">{name}</div>
      <div className="task__duration">
        <div className="task__time">{time_template}</div>
      </div>
    </div>
  );
}

export default Task;
