import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import api from "../../api/api";
import { IPlan } from "../../api/models/Plan";
import { ITask } from "../../api/models/Task";
import { RootState } from "../store";

interface IPlans {
    allPlans : IPlan[]
    currentPlan : IPlan | null
}

const initialState: IPlans = {
    allPlans : [],
    currentPlan : null
}

export const test = createAsyncThunk(
    'plans/test',
    async (data : any/*{
        date : string,
        planName : string,
        tasks : ITask[]
    }*/,thunkAPI) => {
        let state = thunkAPI.getState()
        let newState = JSON.parse(JSON.stringify(state))
    }
)

const PlansState = createSlice({
    name : "plans",
    initialState,
    reducers : {
        setPlans(state, payload:PayloadAction<IPlan[]>){
            state.allPlans = payload.payload
        },

        findPlan(state, payload: PayloadAction<number>) {
            const planIndex = state.allPlans.findIndex(item => item.id == payload.payload)
            const plan = state.allPlans[planIndex]
            console.log(plan)
        },

        addPlan(state, {payload}: PayloadAction<IPlan>) {
            state.allPlans.push(payload)
        },

        selectPlan(state, {payload}: PayloadAction<IPlan | null>) {
            state.currentPlan = payload
        },
 
        deletePlan(state, {payload}: PayloadAction<number>) {
            state.allPlans = state.allPlans.filter((item)=>{
                return item.id != payload
            })
        }
    },

    extraReducers : {
        [String(test.fulfilled)]: (state, action) => {
            
        }
    }
})

const PlansActions = {
    ...PlansState.actions,
    test
}
export default PlansActions
export const PlansReducer = PlansState.reducer